<template>
  <div class="rate-card">
    <UiTextString
      class="rate-card__title"
      size="l"
      weight="700"
      :color="active ? 'white' : ''"
    >
      {{ title }}
    </UiTextString>
    <div class="rate-card__price">
      <UiTextString
        size="xl"
        weight="500"
        fontStyle="italic"
        :color="active ? 'white' : ''"
      >
        {{ price }}
      </UiTextString>
      <UiTextString
        size="xs"
        weight="500"
        fontStyle="italic"
        :color="active ? 'white' : ''"
      >
        ₽/мин
      </UiTextString>
    </div>
    <div class="rate-card__additional">
      <UiTextString
        size="xs"
        weight="500"
        class="rate-card__wait-price"
        :color="active ? 'white' : ''"
      >
        ожидание — {{ waitPrice }} ₽/мин
      </UiTextString>
      <UiTextString
        size="xs"
        weight="500"
        class="rate-card__wait-time"
        :color="active ? 'white' : ''"
      >
        20 мин бесплатного ожидания
      </UiTextString>
    </div>
    <UiButton
      v-if="!active"
      class="rate-card__btn"
      color="white"
      fullwidth
      @click="emitChooseRate"
    >
      Выбрать
    </UiButton>
  </div>
</template>

<script>
import UiTextString from '@/components/core/ui-text-string/ui-text-string.vue';
import UiButton from '@/components/core/ui-button/ui-button.vue';

export default {
  components: {
    UiTextString,
    UiButton,
  },

  props: {
    title: {
      type: String,
      required: true,
    },

    value: {
      type: String,
      required: true,
    },

    price: {
      type: Number,
      required: true,
    },

    waitPrice: {
      type: Number,
      required: true,
    },

    active: {
      type: Boolean,
      required: false,
    },
  },

  methods: {
    emitChooseRate() {
      this.$emit('chooseRate', this.value);
    },
  },
};
</script>

