<template>
  <UiModal opened class="current-ride-modal" @close="$emit('close')">
    <UiContainer class="current-ride-modal__content">
      <UiTextString size="xxl" weight="700" class="current-ride-modal__header">
        Детали поездки
      </UiTextString>
      <div class="current-ride-modal__details">
        <div class="current-ride-modal__col">
          <UiTextString size="l" weight="700">
            {{ rideId }}
          </UiTextString>
          <UiTextString size="m" weight="500" color="grey">
            Номер #
          </UiTextString>
        </div>
        <div class="current-ride-modal__col">
          <UiTextString size="l" weight="700">
            {{ rideDuration }}
          </UiTextString>
          <UiTextString size="m" weight="500" color="grey">
            время поздки
          </UiTextString>
        </div>
        <div class="current-ride-modal__col">
          <UiTextString size="l" weight="700">
            {{ ridePrice }}
          </UiTextString>
          <UiTextString size="m" weight="500" color="grey">
            стоимость
          </UiTextString>
        </div>
      </div>

      <div class="current-ride-modal__profile">
        <div class="current-ride-modal__user">
          <div class="current-ride-modal__avatar">
            <img :src="user.src" alt="" />
          </div>
          <div class="current-ride-modal__user-description">
            <UiTextString
              size="xl"
              weight="700"
              class="current-ride-modal__name"
            >
              {{ user.name }}
            </UiTextString>
            <UiTextString
              size="s"
              weight="500"
              color="grey"
              class="current-ride-modal__ride-count"
            >
              {{ user.rideCount }} поездок
            </UiTextString>
            <ButtonLink class="current-ride-modal__link">Перейти →</ButtonLink>
          </div>
        </div>
        <div class="current-ride-modal__penalty">
          <div
            class="current-ride-modal__sign"
            v-for="p in penalty"
            :key="p.id"
          >
            <img :src="p.src" alt="" />
          </div>
        </div>
      </div>

      <div class="current-ride-modal__actions">
        <UiButton class="current-ride-modal__btn" color="white"
          >Связаться с клиентом</UiButton
        >
        <UiButton class="current-ride-modal__btn" color="white-red"
          >Завершить поездку</UiButton
        >
      </div>
    </UiContainer>
  </UiModal>
</template>

<script>
import UiModal from '@/components/core/ui-modal/ui-modal.vue';
import UiTextString from '@/components/core/ui-text-string/ui-text-string.vue';
import UiButton from '@/components/core/ui-button/ui-button.vue';
import UiContainer from '@/components/core/ui-container/ui-container.vue';
import ButtonLink from '@/components/button-link/button-link.vue';
import convertMsToTime from '@/helpers/convert-ms-to-time';
import convertCurrency from '@/helpers/convert-currency';

export default {
  components: {
    UiModal,
    UiTextString,
    UiButton,
    UiContainer,
    ButtonLink,
  },

  props: {
    rideId: {
      type: String,
      required: true,
    },

    duration: {
      type: Number,
      required: true,
    },

    price: {
      type: Number,
      required: true,
    },

    user: {
      type: Object,
      required: true,
    },

    penalty: {
      type: Array,
      required: false,
      default: () => [],
    },
  },

  computed: {
    rideDuration() {
      return convertMsToTime(this.duration);
    },

    ridePrice() {
      return convertCurrency(this.price);
    },
  },
};
</script>

