import Vue from 'vue';
import Vuex from 'vuex';
import CarService from '@/services/car-page';

Vue.use(Vuex);

export const CAR_PAGE_STORE = 'CAR_PAGE_STORE';

export default {
  namespaced: true,
  state: {
    car: {
      description: {},
      condition: {}
    },
    person: {
      src: '',
      name: ''
    },
    rate: '',
    rideDetails: [],
    rideHistory: [],
    isCarAvailable: true
  },
  actions: {
    async LOAD_DATA({ commit }) {
      const { data } = await CarService.getData();

      commit('SET_DATA', data);
    },

    async CHANGE_RATE({ commit }, rate) {
      commit('CHANGE_RATAE', rate);
    },

    async TOGGLE_CAR_VISIBILITY({ commit }, toggleValue) {
      commit('CHANGE_CAR_VISIBILITY', toggleValue);
    }
  },

  mutations: {
    SET_DATA(state, payload) {
      state.car = payload.car;
      state.person = payload.person;
      state.rate = payload.rate;
      state.rideDetails = payload.rideDetails;
      state.rideHistory = payload.rideHistory;
    },

    CHANGE_RATE(state, payload) {
      state.rate = payload;
    },

    CHANGE_CAR_VISIBILITY(state, payload) {
      state.isCarAvailable = payload;
    }
  },

};
