<template>
  <div class="car-page">
    <HeaderNav />
    <div class="car-page__content">
      <SkeletonProductPageStub v-if="isLoading" />
      <div class="car-page__container" v-else>
        <CarCardSection
          class="car-page__car"
          :car="pageState.car"
          :active="isCarActive"
          @toggleCarVisibility="toggleCarVisibility"
        />
        <CarMap class="car-page__map" />
        <SmallCard
          class="car-page__current-owner"
          :title="pageState.person.name"
          caption="поездка →"
          :src="pageState.person.src"
          :link="'/'"
          @click="showRideModal"
        />
        <SmallCard
          class="car-page__rate"
          :title="i18n[pageState.rate] || ''"
          caption="тариф"
          :link="'/'"
          src="data:image/png;base64,"
          @click="showRateModal"
        />
        <SectionRideWithChartVue class="car-page__rides-chart" />
        <SectionCarRideHistory
          class="car-page__ride-history"
          :rideHistory="pageState.rideHistory"
        />
        <TitleWithChartCard class="car-page__income-chart" />
        <SectionDistanceWithChart class="car-page__distance" />

        <CurrentRideModal
          v-if="rideModalActive"
          :rideId="pageState.rideDetails.id"
          :duration="pageState.rideDetails.duration"
          :user="pageState.rideDetails.user"
          :price="pageState.rideDetails.price"
          :penalty="pageState.rideDetails.penalty"
          @close="closeRideModal"
        />

        <RateModal
          v-if="rateModalActive"
          :rate="pageState.rate"
          @close="closeRateModal"
          @changeRate="changeRate"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex';
import HeaderNav from '@/components/header-nav/header-nav.vue';
import CarCardSection from '@/components/car-card-section/car-card-section.vue';
import CarMap from '@/components/car-map/car-map.vue';
import SmallCard from '@/components/small-card/small-card.vue';
import SectionRideWithChartVue from '@/components/section-ride-with-chart/section-ride-with-chart.vue';
import SectionDistanceWithChart from '@/components/section-distance-with-chart/section-distance-with-chart.vue';
import TitleWithChartCard from '@/components/title-with-chart-card/title-with-chart-card.vue';
import SectionCarRideHistory from '@/components/section-car-ride-history/section-car-ride-history.vue';
import CurrentRideModal from '@/components/current-ride-modal/current-ride-modal.vue';
import RateModal from '@/components/rate-modal/rate-modal.vue';
import pageStore, { CAR_PAGE_STORE } from '@/store/car-page';
import SkeletonProductPageStub from '@/components/skeleton-product-page-stub/skeleton-product-page-stub.vue';

const storeModule = {
  name: CAR_PAGE_STORE,
  module: pageStore,
};

const i18n = {
  buisness: 'Бизнес',
};

export default {
  components: {
    HeaderNav,
    CarCardSection,
    CarMap,
    SmallCard,
    SectionRideWithChartVue,
    SectionDistanceWithChart,
    TitleWithChartCard,
    SectionCarRideHistory,
    CurrentRideModal,
    RateModal,
    SkeletonProductPageStub,
  },
  data: () => ({
    isLoading: false,
    rideModalActive: false,
    rateModalActive: false,
  }),

  beforeCreate() {
    if (!this.$store.hasModule(storeModule.name)) {
      this.$store.registerModule(storeModule.name, storeModule.module);
    }
  },

  async created() {
    this.isLoading = true;
    await this.LOAD_DATA();
    this.isLoading = false;
  },

  beforeDestroy() {
    this.$store.unregisterModule(storeModule.name);
  },

  computed: {
    i18n() {
      return i18n;
    },

    pageState() {
      return this.$store.state[storeModule.name];
    },

    isCarActive() {
      return this.pageState.isCarAvailable;
    },
  },
  methods: {
    ...mapActions(storeModule.name, [
      'LOAD_DATA',
      'CHANGE_RATE',
      'TOGGLE_CAR_VISIBILITY',
    ]),

    showRideModal() {
      this.rideModalActive = true;
    },

    closeRideModal() {
      this.rideModalActive = false;
    },

    showRateModal() {
      this.rateModalActive = true;
    },

    closeRateModal() {
      this.rateModalActive = false;
    },

    changeRate(rate) {
      this.CHANGE_RATE(rate);
    },

    toggleCarVisibility(toggleValue) {
      this.TOGGLE_CAR_VISIBILITY(toggleValue);
    },
  },
};
</script>

<style>
.car-page {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: auto;
  padding-bottom: 90px;
}

.car-page__content {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  width: 1200px;
  margin: 100px auto 0;
}

.car-page__container {
  flex-grow: 1;
  display: grid;
  grid-gap: 25px;
  grid-template-columns: repeat(5, 1fr);
  grid-template-areas:
    'car car car map map'
    'car car car owner rate'
    'rideChart rideChart rideHistory rideHistory rideHistory'
    'incomeChart incomeChart incomeChart distanceChart distanceChart';
}

.car-page__car {
  grid-area: car;
}

.car-page__map {
  grid-area: map;
}

.car-page__current-owner {
  grid-area: owner;
}

.car-page__rate {
  grid-area: rate;
}

.car-page__rides-chart {
  grid-area: rideChart;
}

.car-page__ride-history {
  grid-area: rideHistory;
}

.car-page__income-chart {
  grid-area: incomeChart;
}

.car-page__distance {
  grid-area: distanceChart;
}
</style>
