      <slot />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    opened: {
      type: Boolean,
      required: true,
      default: false,
    },
    slug: {
      type: String,
      required: false,
      default: '',
    },
  },

  computed: {
    isHeadingSlotPresent() {
      return !!this.$slots.heading;
    },
  },

  methods: {
    close() {
      this.$emit('close', this.slug);
    },
  },
};
</script>

