<template>
  <UiModal opened class="rate-modal" @close="$emit('close')">
    <UiContainer class="rate-modal__content">
      <UiTextString size="xxl" weight="700" class="rate-modal__header">
        Тариф
      </UiTextString>

      <div class="rate-modal__container">
        <RateCardDecorator
          v-for="r in rates"
          :key="r.id"
          :active="r.value === rate"
          class="rate-modal__card"
        >
          <CarsharingRateCard
            :title="r.title"
            :value="r.value"
            :price="r.price"
            :waitPrice="r.waitPrice"
            :active="r.value === rate"
            @chooseRate="emitChangeRate"
          />
        </RateCardDecorator>
      </div>
    </UiContainer>
  </UiModal>
</template>

<script>
import UiModal from '@/components/core/ui-modal/ui-modal.vue';
import UiTextString from '@/components/core/ui-text-string/ui-text-string.vue';
import UiContainer from '@/components/core/ui-container/ui-container.vue';
import RateCardDecorator from '@/components/rate-card-decorator/rate-card-decorator.vue';
import CarsharingRateCard from '@/components/carsharing-rate-card/carsharing-rate-card.vue';

const ratesList = [
  {
    value: 'standart',
    price: 8.5,
    waitPrice: 4,
  },
  {
    value: 'buisness',
    price: 15.6,
    waitPrice: 7,
  },
];
const i18n = {
  standart: 'Эконом',
  buisness: 'Бизнес',
};

export default {
  components: {
    UiModal,
    UiTextString,
    UiContainer,
    CarsharingRateCard,
    RateCardDecorator,
  },

  props: {
    rate: {
      type: String,
      required: true,
    },
  },

  computed: {
    rates() {
      return ratesList.map((r) => ({
        value: r.value,
        title: i18n[r.value],
        price: r.price,
        waitPrice: r.waitPrice,
      }));
    },
  },

  methods: {
    emitChangeRate(rate) {
      this.$emit('changeRate', rate);
    },
  },
};
</script>

<style>
.rate-modal__container {
  margin-top: 20px;
  display: flex;
  gap: 16px;
}

.rate-modal__card {
  width: 50%;
}
</style>
