<template>
  <UiContainer class="car-card-section">
    <div class="car-card-section__header">
      <div class="car-card-section__header-left">
        <UiTextString size="xxl" weight="700">{{ car.title }}</UiTextString>
        <UiTextString
          class="car-card-section__type"
          size="l"
          color="grey"
          weight="700"
        >
          {{ car.type }}
        </UiTextString>
      </div>
      <ToggleIosButton
        class="car-card-section__header-right"
        :checked="car.enabled"
        @change="emitChangeToggle"
      />
    </div>

    <div class="car-card-section__container">
      <div class="car-card-section__left">
        <div class="car-card-section__image" :class="carImageClasses">
          <img :src="car.src" alt="" />
        </div>

        <div class="car-card-section__plate">
          <UiTextString size="xl" weight="500">{{ car.number }}</UiTextString>
        </div>
        <div class="car-card-section__description">
          <div
            class="car-card-section__description-item"
            v-for="item in description"
            :key="item.value"
          >
            <UiTextString size="s" color="grey" weight="500"
              >{{ item.caption }}:</UiTextString
            >
            <UiTextString size="s" weight="500">{{ item.title }}</UiTextString>
          </div>
        </div>
      </div>
      <div class="car-card-section__right">
        <ProgressBar
          class="car-card-section__progress-bar"
          :percent="80"
          :lineWidth="10"
          barColor="#007DBC"
          :scaleLength="0"
        >
          <div class="car-card-section__condition">
            <UiTextString size="s" weight="700">
              {{ car.condition.distance }}км
            </UiTextString>
            <UiTextString size="xs" weight="700" color="grey">
              пройдено
            </UiTextString>
          </div>
        </ProgressBar>

        <ProgressBar
          class="car-card-section__progress-bar"
          :percent="12"
          :lineWidth="10"
          barColor="#F94F12"
          :scaleLength="0"
        >
          <div class="car-card-section__condition">
            <UiTextString size="s" weight="700">
              {{ car.condition.gas }}%
            </UiTextString>
            <UiTextString size="xs" weight="700" color="grey">
              бензина
            </UiTextString>
          </div>
        </ProgressBar>

        <ProgressBar
          class="car-card-section__progress-bar"
          :percent="12"
          :lineWidth="10"
          barColor="#F94F12"
          :scaleLength="0"
        >
          <div class="car-card-section__condition">
            <UiTextString size="s" weight="700">
              {{ car.condition.brakes }}%
            </UiTextString>
            <UiTextString size="xs" weight="700" color="grey">
              тормозная <br />
              жидкость
            </UiTextString>
          </div>
        </ProgressBar>
      </div>
    </div>
  </UiContainer>
</template>

<script>
import UiContainer from '@/components/core/ui-container/ui-container.vue';
import UiTextString from '@/components/core/ui-text-string/ui-text-string.vue';
import ToggleIosButton from '@/components/toggle-ios-button/toggle-ios-button.vue';
import ProgressBar from '@/components/progress-bar/progress-bar.vue';

const i18n = {
  color: 'Цвет',
  seat: 'Места',
  weight: 'Масса',
  vin: 'Vin',
  insurance: 'Страховка',
  year: 'Год',
};

export default {
  components: {
    UiContainer,
    UiTextString,
    ToggleIosButton,
    ProgressBar,
  },

  props: {
    car: {
      type: Object,
      required: true,
    },

    active: {
      type: Boolean,
      required: true,
    },
  },

  computed: {
    description() {
      return Object.keys(this.car.description).map((key) => ({
        caption: i18n[key],
        title: this.car.description[key],
      }));
    },

    carImageClasses() {
      return {
        'car-card-section__image_disabled': !this.active,
      };
    },
  },

  methods: {
    emitChangeToggle(toggleValue) {
      this.$emit('toggleCarVisibility', toggleValue);
    },
  },
};
</script>

