  display: flex;
  align-items: baseline;
  justify-content: space-between;
}

.car-card-section__type {
  margin-top: 5px;
}

.car-card-section__container {
  margin-top: 20px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.car-card-section__image {
  height: 235px;
  transition: opacity .25s ease;
}

.car-card-section__image_disabled {
  opacity: .3;
}

.car-card-section__plate {
  margin-top: 20px;
  display: inline-block;
  background: #f4f4f4;
  border-radius: 10px;
  padding: 7px 8px 7px;
}

.car-card-section__description {
  margin-top: 15px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
}

.car-card-section__description-item {
  display: flex;
  align-items: center;
  gap: 5px;
